import axios from 'axios';
import api from '../config'

export const getCategories = async () =>  {
    const token = await localStorage.getItem('apitoken');
    const config = {
        headers: {
            'Access-Control-Allow-Origin': '*',
            'Accept' : '*/*',
            'host': 'arbam-test.herokuapp.com',
            'Content-Type':'application/x-www-form-urlencoded',
            'Authorization': token,
        }
        }
    try {
    const response = await axios.get(api.server+'/api/categorias',config);
    console.log(response.data);
    return response.data;
    } catch (err) {
        console.log(err)
    }
}

