import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CBadge,
  CDataTable,
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CAlert,
} from '@coreui/react'
import ModalForm from '../../../components/ModalForm/ModalForm';
import ModalWarning from '../../../components/ModalWarning/ModalWarning';

import {getCategories, deleteCategory, createCategory, updateCategory} from '../../../services/categoriesService';


const getBadge = highlight => {
  switch (highlight) {
    case true: return 'success'
    case false: return 'danger'
    default: return 'primary'
  }
}
const fields = [{key: 'name', value: 'nombre', sorter: true},'actions']
const fields1 = { name:'Nombre', actions: 'Acciones'}
const columnsFilter = { name:'Nombre', description: 'Descripcion', financial_knowledge: 'conocimiento Financiero', value: 'valor', highlight: 'Destacado' }
// const fields2 = ['name']

const CoursesCategories = ({history}) => {
  const [visibleForm, setVisibleForm] = useState(false);
  const [data, setData] = useState([]);
  const [deleteRow, setDeleteRow] = useState(false);
  const [isEditing, setEditing] = useState(false)
  const [error, setError] = useState(false)
  const [idForDelete, setIdForDelete] = useState(null);
  const [loading, setLoading] = useState(null);
  const [values, setValues] = useState({
    id: '',
    name: '',
  });
  const toggle = ()=>{
    setValues({
        name: '',
    })
    setVisibleForm(!visibleForm);
    setError(false);
    setEditing(false);
}
 useEffect (() => {
  handleLogout();
  getData();
},[])


const handleLogout = async () => {
    const token =  await localStorage.getItem('apitoken');
    if(token===null || token === undefined){
      history.push('/login');
    }
   }

 const getData = async () => {
   try {
    setLoading(true);
    const response = await getCategories();
    await setData(response.data);
    if(response.data.length > 0) await setLoading(false);
   } catch(err){
     console.log(err)
   }
 }

 const storeCategory = async () => {
    let category = new FormData();
    if(values.name===''){
        setError(true)
    }else{
        category.append('name', values.name);
    try {
        const response = await createCategory(category)
        if(response) {
            console.log(response.data, 'usuario creado');
            setVisibleForm(!visibleForm);
            await getData();
            setError(false)
        }
    } catch(err){
        console.log(err)
    }
}
 }

 const deleteItem = (id) => {
    setDeleteRow(!deleteRow);
    setIdForDelete(id)
  }

  const handleDelete = async (id) => {
     console.log(id,'si entra al delete')
     try {
    if(id!==null){
    await deleteCategory(id)
  }
    } catch (err) {
        console.log(err);
    }
    setDeleteRow(!deleteRow);
    await getData();
  }

  const handleEdit = (item) => {

    setVisibleForm(!visibleForm);
    setEditing(true);
    setError(false);
    setValues({...values,
        id: item.id,
        name: item.name,
      })

  }

  const updateCat = async () => {
    let category = new FormData();
    category.append('name', values.name);
    if(values.id !== '' && values.name!=='' ){
          try {

            const response = await updateCategory(category, values.id)
            if(response) {
                console.log(response.data, 'usuario creado');
                setVisibleForm(!visibleForm);
                await getData();
                setEditing(false);
            }
          } catch(err) {
              console.log(err);
          }
      } else {
          setError(true);
      }
  }

  const handleChange = (e) => {
    e.preventDefault();
    setValues({...values,
      [e.target.name] : e.target.value
    })
    console.log(values);

  }

  return (
    <>
      <CCard>
        <CCardHeader>
          {/* <DocsLink href="https://coreui.io/docs/utilities/colors/"/> */}
          <CButton
          color="primary"

      onClick={() => toggle()}
      className="mr-1"
    >Agregar Categoría</CButton>
        </CCardHeader>
        <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Categorías de Cursos
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={data}
              fields={fields}
              tableFilter={{label: 'Buscar', placeholder: 'Buscar Categoria'}}
              itemsPerPage={10}
              columnHeaderSlot={fields1}
              loading={loading}
              pagination
              striped
              responsive
              scopedSlots = {{
                'actions':
            (item) => (
              <td>
                <CButton style={{marginRight: 10}} onClick={() => handleEdit(item)} color="warning">Editar</CButton>
                <CButton onClick={() => deleteItem(item.id)} color="danger">Eliminar</CButton>
              </td>
            ),
            'phone_number':
            (item) => (
            <td>{`${item.area_code} ${item.phone_number}`}</td>
            )
            }
          }
            />
            </CCardBody>
          </CCard>
        </CCol>
      </CCard>
      <ModalForm visible={visibleForm} modalToggle={toggle} onSubmit={!isEditing ? () => storeCategory() : () => updateCat()} title="Categoria" >
      <CCol xs="12" sm="12">
          <CCard>
            <CCardBody>
              <CFormGroup>
                <CLabel htmlFor="name">Nombre</CLabel>
                <CInput id="name" name="name" value={values.name} onChange={(e) => handleChange(e)} placeholder="Nombre de la categoria" />
              </CFormGroup>
              { error ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Debes ingresar todos los campos!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                {/* <CFormGroup row>
                  <CLabel col md="3" htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile id="image" name="image"/>
                  </CCol>
                </CFormGroup> */}
            </CCardBody>
          </CCard>
        </CCol>
      </ModalForm>
      <ModalWarning visible={deleteRow} onSubmit={() => handleDelete(idForDelete)} modalToggle={() => deleteItem(null)} title="Eliminar Categoria">Estas seguro de eliminar esta categoría?</ModalWarning>

    </>
  )
}

export default CoursesCategories
